<!--登录页面-->
<template>
  <div>
    <el-row class="warp">
      <el-col :span="24" class="panle">
       <el-col :span="12" class="box1">
        <div>
           <h1>猪小能</h1>
           <p>您的效率，就是我们的价值，立即体验</p>
           <p>感受不一样的工作方式</p>
           </div>
       </el-col>
       <el-col :span="12" class="box2">
        <div class="login">
         <el-form>
            <el-input
            type="text" 
            prefix-icon="iconfont icon icon-wode" 
            class="input_class"
            ></el-input>
            <el-input
            type="password"
             prefix-icon="iconfont icon icon-suo"
             ></el-input>
         </el-form>
            <p class="title">没有小猪账号，<span><router-link :to="{name:'sign'}" class="routerclass">立即注册</router-link></span></p>
            <el-button type="primary" class="btn">登录</el-button>
            <p class="content"><span style="color:#989898"><el-switch
                     v-model="value2"
                     active-color="#13ce66"
                     inactive-color="#ff4949"
                     style="margin-right:5px"
                     >
                     </el-switch>记住密码</span><router-link :to="{name:'home'}" class="routerclass"><span style="color:#989898">忘记密码？</span></router-link></p>  
        </div>         
       </el-col>   
      </el-col>  
    </el-row>
    <Footer></Footer>
  </div>
</template>

<script>
import Footer from '../public/footer'
export default {
  data () {
    return {
      value2: false
    };
  },
  components:{
      Footer
  }
}

</script>

<style scoped>
.warp{
    background: url('../../assets/bg2.png') no-repeat;
    background-size: 100% 100%;
}
.panle{
   background-color: rgba(0,0,0,0.5);
   width: 100%;
   color: #fff;
   height: 565px;
}
.box1{
    display: flex;
    justify-content: center;
    height: 100%;
}
.box1 div{
    margin-top: 140px;
}
.box1 div h1{
    margin-bottom: 10px;
}
.box1 div p{
    margin-bottom: 10px;
}
.box2{
    height:100%;
    position: relative;
}
.box2 .login{
    padding: 40px 20px;
    width: 300px;
    height: 240px;
    border-radius: 10px;
    position: absolute;
    top: 25%;
    right: 20%;
    background-color: #fff;
    padding-bottom: 0;
}
.input_class{
    margin-bottom: 10px;
}
.title{
    margin-top: 20px;
    font-size: 12px;
    color: #a1a1a1;
    text-align: right;
}
.routerclass{
    text-decoration: none;
    color: #20acff;
}
.btn{
    width: 100%;
    margin-top: 20px;
    font-size: 18px;
}
.content{
    margin-top: 10px;
    font-size: 12px;
    display: flex;
    justify-content: space-between;
}

</style>


