<!--注册页面-->
<template>
  <div>
    <el-row class="warp">
     <el-col :span="24" class="panel">
       <div class="signbox">
         <h3>猪小能注册</h3>
          <el-form 
           status-icon
           class="formbox" 
           :model="signForm" 
           ref="signForm" 
           :rules="signRules"
           @submit.native.prevent="handleSign" 
           >

          <el-form-item prop="userName" class="form_item">
          <el-input
           placeholder="请输入手机号"
           class="input_1"
           type="text"
           prefix-icon="iconfont icon icon-wode"
           v-model="signForm.userName" 
           ></el-input>
           </el-form-item>

           <el-form-item prop="validateCode" class="form_item auth">
           <el-input
           v-model="signForm.validateCode"
           placeholder="请输入验证码"
           type="text"
           style="width:60%"
           ></el-input>
           <el-button type="primary" @click.native.prevent="sendvalidateCode(signForm.userName)">发送验证码</el-button>
           </el-form-item> 

           <el-form-item prop="passWord" class="form_item">
           <el-input
            v-model="signForm.passWord"
            placeholder="请输入密码"
            class="input_3"
            type="password"
            prefix-icon="iconfont icon icon-suo"
           ></el-input>
           </el-form-item>

           <div>
             <el-switch
              v-model="readed_value"
              active-color="#13ce66"
              inactive-color="#ff4949">
            </el-switch><span class="read" style="margin-left:10px">我已阅读并同意<router-link :to="{name:'home'}" class="router_class">《用户协议》</router-link></span>
           </div>

           <el-button  @click.native.prevent="handleSign('signForm')" class="btn" style="width:100%" type="primary">注册</el-button>
           <p class="tosign">已有账号,<a href="http://115.29.212.104/#/login/" target="_blank">立即登录</a></p>
           <!--第三方登录 
             <div class="icon_box">
             <i class="iconfont icon icon-QQ icon_nor"></i>
             <i class="iconfont icon icon-weixin icon_nor"></i>
             <i class="iconfont icon icon-weibo icon_nor"></i>
             </div>
           -->
         </el-form>
       </div>
     </el-col>
    </el-row>
   <Footer></Footer>
  </div>
</template>

<script>
import axios from 'axios'
window.axios = axios;
import Footer from '../public/footer'
export default {
  data () {
    //前端校验规则
    let checkName=(rule,value,callback)=>{
      var regExp = /^1[3|5|7|8|9][0-9]{9}$/;
        if (value === '') {
            callback(new Error('请输入手机号码'));
        }else if (regExp.test(value) === false) {
            callback(new Error('请输入正确手机号码'))
        } else {
            callback();
        }
    };

    let checkvalidateCode=(rule,value,callback)=>{
      var regExp = /^[0-9]{4,6}$/;
        if (value === '') {
            callback(new Error('请输验证码'));
        }else if (regExp.test(value) === false) {
            callback(new Error('请输入正确验证码'))
        } else {
            callback();
        }
    };  

    let checkPass=(rule,value,callback)=>{
       var regExp = /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,16}$/;
        if (value === '') {
          callback(new Error('请输入6-16位的密码'))
        } else if (regExp.test(value) === false) {
           callback(new Error('6-16字母和数字组成，不能是纯数字或纯英文'))
        } else {
          callback()
               }
    };
      
    return {
      signForm:{
        userName:'',
        passWord:'',
        validateCode:''
      },
      signRules:{
         userName: [
            {validator:checkName, trigger: 'blur'}
          ],
          validateCode:[
            {validator:checkvalidateCode, trigger: 'blur'}
          ],
          passWord:[
            {validator:checkPass, trigger: 'blur'}
          ]
      },
      readed_value:'false',
      btntext:'发送验证码'
    };
  },
  components:{
    Footer
  },
  methods:{ 
    //提交用户注册信息 
    handleSign(signForm){
      
      if (!this.readed_value){
           this.$message.error('请先同意《用户协议》')
      }else{
        this.$refs[signForm].validate((valid)=>{
           if (valid){
             let _this=this;
               //注册接口调用
               //  axios.post("http://112.124.108.253:8765/api/auth/v1/user/register/",
            axios({
                 method:'post',
                 url: "/api/auth/v1/user/register/",
                 headers:{
                   //'accept':'application/json',
                   //'Content-type':'application/x-www-form-urlencoded'
                   //'Content-type':'multipart/form-data'
                   //'Content-type':'text/plain'
                 },
                 params:{
                 userName:_this.signForm.userName,
                 validateCode:_this.signForm.validateCode,
                 password:_this.signForm.passWord
                  }
                     }).then((res)=>{
                 //数据交互成功
                console.log(res);
                if (res.status===200) {
                  this.$message(
                    {
                      type:'sucess',
                      message:'恭喜您，注册成功'
                    })
                  setTimeout(() => {
                    this.$router.push('path:/');
                  }, 1500);
                }

                if (res.status===45002) {
                    this.$message(
                    {
                      type:'sucess',
                      message:'此手机号已注册！'
                    })
                }

             }).catch((error)=>{
                //数据交互失败 
                this.$message.error('注册失败，注册信息有误'); 
                console.log(error)
             })

           }
        })
      } 

     },
    //获取手机验证码
    sendvalidateCode(userName){

      axios.get("http://112.124.108.253:8765/api/sms/v1/user/register/"+userName).then((res)=>{
                 if(res.status===200){
                  this.$message({
                   type:'sucess',
                   message: '验证码发送成功'
                              });
                 }
                }).catch((error)=>{
                 this.$message.error('验证码发送失败，请重新发送'); 
                })
        }
      }
}
</script>

<style scoped>
.warp{
    background: url(https://timgsa.baidu.com/timg?image&quality=80&size=b10000_10000&sec=1527731934&di=906e71c2b1137535c8d95aa6904d11dd&src=http://img.taopic.com/uploads/allimg/140530/318753-1405300G54295.jpg) no-repeat;
    background-size: 100% 100%;
}
.panel{
   background-color: rgba(0,0,0,0.5);
   width: 100%;
   height: 660px;
   display: flex;
   justify-content: center;
}
.signbox{
  width:310px;
  height:310px;
  background-color: #fff;
  margin-top: 12%;
  padding: 15px 30px 20px 30px;
  border-radius: 10px;
}
.form_item{
  width: 100%;
  height: 40px;
}
.signbox h3{
  text-align: center;
  margin-bottom: 10px;
}
.auth{
  display: flex;
  justify-content: space-between;
}
.signbox p{
  text-align: right;
  font-size: 11px;
}
.btn{
  margin: 10px 0;
}
.icon_box{
  display: flex;
  width: 100%;
  justify-content: space-around;
}
.icon_nor{
  font-size: 32px;
  color:#20acff;
  cursor: pointer;
}
.read{
  font-size: 11px;
}
.router_class{
  text-decoration: none;
  color: #20acff;
}
.tosign a{
  text-decoration: none;
  color: #20acff;
}
</style>


