import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Home from '@/components/pages/home'
import Login from '@/components/pages/login'
import Platform from '@/components/pages/platform'
import Contact from '@/components/pages/contact'
import Partner from '@/components/pages/partner'
import Case from '@/components/pages/causa'
import Feature from '@/components/pages/feature'
import Srevice from '@/components/pages/srevice'
import Sign from '@/components/pages/sign'
import Signspare from '@/components/pages/signspare'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },{
      path: '/hellow',
      name: 'hellow',
      component: HelloWorld
    },{
      path: '/login',
      name: 'login',
      component: Login
    },{
      path: '/platform',
      name: 'platform',
      component: Platform,
    },{
      path: '/contact',
      name: 'contact',
      component: Contact,
    },{
      path: '/partner',
      name: 'partner',
      component: Partner,
    },{
       path:'/case',
       name:'case',
       component:Case,
    },{
      path: '/feature',
      name: 'feature',
      component: Feature,
    },{
      path: '/srevice',
      name: 'srevice',
      component: Srevice,
    }, {
      path: '/sign',
      name: 'sign',
      component: Sign,
    },{
      path:'/signspare',
      name:'signspare',
      component:Signspare
    }
  ]
})



