</template>

<script>
import Footer from '../public/footer'
export default {
  data () {
    return {
         tabsName: [{  
                    name: "海量接口",  
                    isActive: true  
                }, {  
                    name: "线下服务",  
                    isActive: false  
                }, {  
                    name: "SaaS服务",  
                    isActive: false  
                },{  
                    name: "用户流共享",  
                    isActive: false  
                },{  
                    name: "开发指导",  
                    isActive: false  
                }],  
                active: false,
    };
  },
  components:{
      Footer
  },
   methods: {  
        tabsSwitch: function(tabIndex) {  
            var tabCardCollection = document.querySelectorAll(".tab-card"),  
            len = tabCardCollection.length;  
            for(var i = 0; i < len; i++) {  
                tabCardCollection[i].style.display = "none";  
                this.tabsName[i].isActive = false;  
                }  
                this.tabsName[tabIndex].isActive = true;  
                tabCardCollection[tabIndex].style.display = "block";  
                }  
            }  
        
}
</script>

<style scoped>
.tabs{
    margin: 0 auto;
    width: 70%;
    display: flex;
    justify-content: space-around
}
.tabs li{  
   list-style: none;  
}
.tabs .tab-link{
    border: none;   
}             
.tabs .tab-link.active {  
background-color: #2e9bff;
color:#fff;  
transition: .4s;  
}  
.cards {  
width: 100%;
display: flex;
justify-content: center;
}           
.cards .tab-card {  
display: none;  
}
.cards .tab-card{
    width: 78%;
}           
.box1 img{
    height: 635px;
    border: none;
    margin: 0 auto;
} 
.box{
    width: 100%;
	height: 635px;
	text-align: center;
	position: relative;
	overflow: hidden;
}
.box img{
    height: 635px;
	margin: 0 -100%;
}
.box1 h1{
    text-align: center;
    margin: 60px 0;
    font-weight: normal;
}
.box1{
    border-bottom: 2px solid #eef6f6;
}
.box2{
    height: 640px;
    background-color: #fff;
}
.tab-card ul{
    list-style: none;
    display: flex;
    justify-content: space-around;
    margin-top: 80px;
}
.iconfont{
    color: #2e9bff;
    font-size: 35px;
}
.contentbox{
    width: 260px;
}
.contentbox p{
    text-align: center;
    margin:10px 0;
}
.contentbox p:nth-child(3){
    color: #a8a8a8;
    text-align: left;
}
</style>


